<template>
  <div class="iPhone" id="iPhone">
    <div class="iPhone-container" @click="analyticsIphone">
      <font-awesome-icon id="times" :icon="['fas', 'times']" />
      <div class="flex">
          <p>Install this webapp on your iPhone: Tap <span><img :src="require(`@/assets/images/share.png`)" /></span> and then Add to Home Screen <font-awesome-icon :icon="['fas', 'plus-square']" /></p>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'iPhoneBanner',
  data(){
    return{
      userAgent: null,
      agent: null,
      standAlone: false
    }
  },
  props:{
      go: String,
      book: String
  },
  methods:{
    analyticsIphone(){
      this.$ga.event('clicks', 'iPhone', 'iPhone alert', 124);
      $('#iPhone').fadeOut(100);
    },
    isIOS(){
      // Detects if device is on iOS 
      this.userAgent = window.navigator.userAgent.toLowerCase();
      this.agent = /iphone|ipad|ipod/.test(this.userAgent);

      if(window.navigator.standalone === true){
        this.standAlone = true;
      }
    }
  },
  mounted(){
    
    this.isIOS();

    // Checks if should display install popup notification:
    if (this.agent == false) {
      document.getElementById('iPhone').style.display = 'none';
    }

    if (this.standAlone == true){
      document.getElementById('iPhone').style.display = 'none';
    }
    
  }
}
</script>

