             <router-link to="/"><button @click="scrollUp" class="cta" v-if="count === 9">Done</button></router-link>
            <button @click="counterPlus" class="next" v-if="count != 9">Next</button>
            <a @click="counterSub" class="previous" v-if="count != 1">Previous</a>
            <div class="rosary">
                <img :src="getImg()" v-bind:alt="getImg()">
            </div>
