<script>
    //npm i jquery  
    import $ from 'jquery'

    export default{
        name: 'PrayerComponent',
        data(){
            return{
                count: 1,
                countDecade: 1,
                bloodAndWater: 0,
                doxology: 1,
                decadeOne: 0,
                decadeTwo: 0,
                decadeThree: 0,
                decadeFour: 0,
                decadeFive: 0
            }
        },
        methods:{
            counterPlus(){
                if (this.count === 2){ //If 2 special case
                    if(this.bloodAndWater >= 3){
                        this.count++;
                        this.bloodAndWater = 3;
                    } else{
                        this.bloodAndWater++;
                        //pause and reset audio
                        document.querySelector('.bloodAndWaterAudio').pause();
                        document.querySelector('.bloodAndWaterAudio').currentTime = 0;
                    }
                } else if (this.count === 6){ //If 6 special case
                    if (this.countDecade === 1){
                        if(this.decadeOne >= 10){
                            this.decadeOne = 10;
                            this.countDecade++;
                        } else{
                            this.decadeOne++;
                            //pause and reset audio
                            document.querySelector('.decadeAudio').pause();
                            document.querySelector('.decadeAudio').currentTime = 0;
                        }
                    } else if (this.countDecade === 2){
                        if(this.decadeTwo >= 10){
                            this.decadeTwo = 10;
                            this.countDecade++;
                        } else{
                            this.decadeTwo++;
                            //pause and reset audio
                            document.querySelector('.decadeAudio').pause();
                            document.querySelector('.decadeAudio').currentTime = 0;
                        }
                    } else if (this.countDecade === 3){
                        if(this.decadeThree >= 10){
                            this.decadeThree = 10;
                            this.countDecade++;
                        } else{
                            this.decadeThree++;
                            //pause and reset audio
                            document.querySelector('.decadeAudio').pause();
                            document.querySelector('.decadeAudio').currentTime = 0;
                        }
                    } else if (this.countDecade === 4){
                        if(this.decadeFour >= 10){
                            this.decadeFour = 10;
                            this.countDecade++;
                        } else{
                            this.decadeFour++;
                            //pause and reset audio
                            document.querySelector('.decadeAudio').pause();
                            document.querySelector('.decadeAudio').currentTime = 0;
                        }
                    } else if (this.countDecade === 5){
                        if(this.decadeFive >= 10){
                            this.decadeFive = 10;
                            this.count++;
                        } else{
                            this.decadeFive++;
                            //pause and reset audio
                            document.querySelector('.decadeAudio').pause();
                            document.querySelector('.decadeAudio').currentTime = 0;
                        }
                    }
                } else if (this.count === 7){ //If 7 special case
                    if(this.doxology >= 3){
                        this.count++;
                        this.doxology = 3;
                    } else{
                        this.doxology++;
                        //pause and reset audio
                        document.querySelector('.holyGodAudio').pause();
                        document.querySelector('.holyGodAudio').currentTime = 0;
                    }
                } else{ //Main addition
                    this.count++;
                }

                //ScrollTop
                var target = $('#prayer');
                if (target.length){
                    var top = target.offset().top;
                    $('html,body').animate({scrollTop: top}, 200);
                    return false;
                }

                this.$ga.event('clicks', 'next', 'next link', 125);

            },
            counterSub(){
                if (this.count > 1){//Stop from past beginning
                    if (this.count === 2){ //If 2 special case
                        if(this.bloodAndWater < 1){
                            this.count--;
                            this.bloodAndWater = 0;
                        } else{
                            this.bloodAndWater--;
                            //pause and reset audio
                            document.querySelector('.bloodAndWaterAudio').pause();
                            document.querySelector('.bloodAndWaterAudio').currentTime = 0;
                        }
                    } else if(this.count === 6){ //If 6 special case
                        if (this.countDecade === 1){
                            if(this.decadeOne < 1){
                                this.count--;
                                this.decadeOne = 0;
                            } else{
                                this.decadeOne--;
                                //pause and reset audio
                                document.querySelector('.decadeAudio').pause();
                                document.querySelector('.decadeAudio').currentTime = 0;
                            }
                        } else if (this.countDecade === 2){
                            if(this.decadeTwo < 1){
                                this.countDecade--;
                                this.decadeTwo = 0;
                            } else{
                                this.decadeTwo--;
                                //pause and reset audio
                                document.querySelector('.decadeAudio').pause();
                                document.querySelector('.decadeAudio').currentTime = 0;
                            }
                        } else if (this.countDecade === 3){
                            if(this.decadeThree < 1){
                                this.countDecade--;
                                this.decadeThree = 0;
                            } else{
                                this.decadeThree--;
                                //pause and reset audio
                                document.querySelector('.decadeAudio').pause();
                                document.querySelector('.decadeAudio').currentTime = 0;
                            }
                        } else if (this.countDecade === 4){
                            if(this.decadeFour < 1){
                                this.countDecade--;
                                this.decadeFour = 0;
                            } else{
                                this.decadeFour--;
                                //pause and reset audio
                                document.querySelector('.decadeAudio').pause();
                                document.querySelector('.decadeAudio').currentTime = 0;
                            }
                        } else if (this.countDecade === 5){
                            if(this.decadeFive < 1){
                                this.countDecade--;
                                this.decadeFive = 0;
                            } else{
                                this.decadeFive--;
                                //pause and reset audio
                                document.querySelector('.decadeAudio').pause();
                                document.querySelector('.decadeAudio').currentTime = 0;
                            }
                        }
                    } else if (this.count === 7){ //If 7 special case
                        if(this.doxology <= 1){
                            this.count--;
                            this.doxology = 1;
                        } else{
                            this.doxology--;
                            //pause and reset audio
                            document.querySelector('.holyGodAudio').pause();
                            document.querySelector('.holyGodAudio').currentTime = 0;
                        }
                    } else{ //Main subtraction
                        this.count--;
                    }
                } else{
                    console.log('At beginning of prayer');
                }

                //ScrollTop
                var target = $('#prayer');
                if (target.length){
                    var top = target.offset().top;
                    $('html,body').animate({scrollTop: top}, 200);
                    return false;
                }

                this.$ga.event('clicks', 'previous', 'previous link', 126);
            },
            getImg() {
                var images = require.context('../assets/images/rosary', false, /\.png$/)
                if (this.count < 6 || this.count > 6){
                    return images('./rosary_' + this.count + ".png")
                } else if (this.count === 6){
                    if (this.countDecade === 1){
                        return images('./rosary_' + this.count + '.'+ this.countDecade +'.' + this.decadeOne + ".png")
                    } else if (this.countDecade === 2){
                        return images('./rosary_' + this.count + '.'+ this.countDecade +'.' + this.decadeTwo + ".png")
                    } else if (this.countDecade === 3){
                        return images('./rosary_' + this.count + '.'+ this.countDecade +'.' + this.decadeThree + ".png")
                    } else if (this.countDecade === 4){
                        return images('./rosary_' + this.count + '.'+ this.countDecade +'.' + this.decadeFour + ".png")
                    } else if (this.countDecade === 5){
                        return images('./rosary_' + this.count + '.'+ this.countDecade +'.' + this.decadeFive + ".png")
                    }
                }
            },
            scrollUp(){
                $('html,body').animate({scrollTop: 0}, 100);
            }
        },
        computed: {
            console: () => console,
            window: () => window,
        },
    }
</script>

