<template>
  <div class="menu">
    <router-link id="homeButton" class="menuButton" :to="go" @click="analyticsHome"><font-awesome-icon :icon="['fas', 'home']" /></router-link>
     <router-link id="novenaButton" class="menuButton" :to="novena" @click="analyticsNovena"><font-awesome-icon :icon="['fas', 'heart']" /></router-link>
    <router-link id="aboutButton" class="menuButton" :to="about" @click="analyticsAbout"><font-awesome-icon :icon="['fas', 'info-circle']" /></router-link>
  </div>
</template>

<script>
export default {
  name: 'MainMenu',
  props:{
      go: String,
      about: String,
      novena: String
  },
  methods:{
    analyticsHome(){
      this.$ga.event('clicks', 'home', 'home icon', 120);
    }, 
    analyticsAbout(){
      this.$ga.event('clicks', 'about', 'about icon', 121);
    },
    analyticsNovena(){
      this.$ga.event('clicks', 'novena', 'novena icon', 122);
    },
  }
}
</script>

